
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


html, body {
  height: 100%;
  width: 100%;
  margin:0;
  padding:0;
}

body {
  height: 100%;
  width: 100%;
  margin:0;
  padding:0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.transition-wrapper {
  position: relative;
  z-index: 1;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  width: 100%;
}

.transition-wrapper .transition-item {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}


/* Enter */
.detail-page {
  overflow: auto;
  box-sizing: border-box;
  /* background-color: #03a9f4; */
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
}

.detail-page.transition-appear {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

.detail-page.transition-appear.transition-appear-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.detail-page.transition-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.detail-page.transition-leave.transition-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}



.speech-bubble-intro {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 3rem 2rem;
  margin-top: 4rem;
  background: #f5f5f5;
  border-radius: .4em;
  box-shadow: 0 0 20px #cbcbcb;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  margin-bottom: 10rem;
  width: 600px;
}

.speech-bubble-intro:after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-bottom-color: #f5f5f5;
  border-top: 0;
  margin-left: -20px;
  margin-top: -20px;
}


.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 250px;
}

.survey-button {
  transition: all 0.05s linear;
}


.survey-button:hover, .survey-button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.speech-bubble {
  font-family: 'Montserrat', Arial;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 1rem;
  position: relative;
  background: #f5f5f5;
  border-radius: 1em;
  box-shadow: 0 0 20px #cbcbcb;
  height: 200px;
  width: 400px;
}

.speech-bubble:after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-right-color: #f5f5f5;
  /*box-shadow: 0 0 10px #929292;*/
  border-left: 0;
  margin-top: -20px;
  margin-left: -20px;
}


.surveypain-button {
  transition: all 0.05s linear;
}

.surveypain-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.surveypain-button:active {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}



.calendar-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: 125px;
}



.nav-item {
  color: #767676;
}

.nav-item:hover, .nav-item:active {
  color: #3cbba5;
}

